@import url('https://fonts.googleapis.com/css2?family=Great+Vibes&family=Neonderthaw&family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;


.gradient-bg {
  background-color:#212121;
  background-image: 
    radial-gradient(at 0% 0%, hsl(300, 0%, 40%,1) 0, transparent 50%), 
    radial-gradient(at 50% 0%, hsl(224, 15%, 24%,1) 0, transparent 50%), 
    radial-gradient(at 100% 0%, hsl(0, 0%, 39%,1) 0, transparent 50%);
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.text-gray-100 {
  color: #f5f5f5;
}

.text-gray-300 {
  color: #e0e0e0;
}

.text-gray-400 {
  color: #bdbdbd;
}

.bg-gray-400 {
  background-color: #bdbdbd;
}

body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}



